	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	import avatar from "../../yq-avatar.vue";
	export default {
		data() {
			return {
				array: ["供应信息","需求信息"],
				array1:[0,1],
                index: "",
				selectValue:"", 
				imageList:[],
				title:"",
				textarea:"",
				pathArr:[],
				listPath:[],
				phone:"",
				percent:0,
				showBox:false,
				state:false,
				id:'',
				smallPrimary:'../../../static/img/timg1.jpg',
				myId:'',
			}
		},
		onLoad:function(option){
			var _self = this
			console.log(option)
			_self.id = option.id
			_self.branch = option.branch
			_self.mark = option.mark
			console.log(_self.imageList)
			_self.getInfo()
		},
		methods: {
			getInfo:function(){
				var _self = this
				uni.request({ 
					url:_self.$api+"dockingManager/declareNewQuery",
					data:{id:_self.id,optionId:uni.getStorageSync("openId"),branch:_self.branch,mark:_self.mark},
					method:"GET",
					success:function(res){
						console.log(res)
						_self.title = res.data[0].name
						_self.phone = res.data[0].phone
						_self.textarea = res.data[0].sketch
						_self.smallPrimary = res.data[0].small_primary
						if( res.data[0].shrink == ''){
							_self.imageList = []
						}else{
							_self.imageList = res.data[0].shrink.split(',')
						}
						_self.pathArr = _self.imageList
					}
				})
			},
			updatePic:function(){
				var _self = this
				console.log(_self.listPath)
				_self.listPath.push({id:_self.id})
				uni.request({
					url:_self.$api+"dockingManager/titleChartUpdate",
					data:JSON.stringify(_self.listPath),
					method:"POST",
					success:function(res){
						if(res.data==1){
							console.log('图片修改成功')
						}else{
							console.log('图片修改失败')
							uni.showToast({
								title:'图片修改失败，请重试',
								icon:'none'
							})
							return false
						}
					}
				})
			},
			updateContent:function(){
				var _self = this
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
				if(_self.title == ""){
					uni.showToast({
						title:"请输入标题",
						icon:"none"
					})
					return false
				}
				// else if(_self.state == false && _self.imageList.length>0){ 
				// 	uni.showToast({
				// 		title:"图片正在上传，请稍后提交1",
				// 		icon:"none"  
				// 	})
				// 	return false
				// }
				else if(_self.textarea == ""){
					uni.showToast({
						title:"请输入简介",
						icon:"none"
					})
					return false
				}
				else if(_self.phone == ""){
					uni.showToast({
						title:"请输入联系方式",
						icon:"none"
					})
					return false
				}
				else if(_self.smallPrimary == ""){
					uni.showToast({
						title:"请上传缩略图",
						icon:"none"
					})
					return false
				}
				else if(!myreg.test(_self.phone)){
						uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}
					for(var i =0;i<_self.pathArr.length;i++){
						_self.listPath.push({testName1:_self.pathArr[i]})
						console.log(_self.listPath)
					}
					_self.updatePic()
				
					console.log(JSON.stringify(_self.listPath))
				uni.request({
					url:_self.$api+"dockingManager/titleDeclareUpdate", 
					data:{
						id:_self.id,
						phone:_self.phone,
						mark:_self.mark,
						name:_self.title,
						sketch:_self.textarea,
						branch:uni.getStorageSync('tradeId'),
						trade:0,smallPrimary:_self.smallPrimary
					}, 
					method:"GET",
					success:function(res){ 
						console.log(res)
						if(res.data ==98){
							uni.showToast({
								title:"您尚未认证",
								icon:'none'
							})
							return false
						}else if(res.data ==99){
							uni.showModal({
							title: '提示',
							content: '此操作需用户授权，是否进行授权',
							success: function (res) {
								if (res.confirm) {
									//跳转到授权页面  
									uni.navigateTo({
										url:"../../login/login"  
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
						
							return false
						}else if(res.data ===0){
								uni.showToast({
								title:"修改失败"
						})
							return false
						}else if(res.data ==1){
								uni.showToast({
								title:"修改成功",
								success:function(){
									setTimeout(function() {
										uni.navigateBack({
											delta: 1,
											animationType: 'pop-out',
											animationDuration: 100
										});
									}, 1000);
								}
							})
							return false
						}else{
							uni.showToast({
								title:"系统异常"
							})
							return false
						}
						console.log(res)
					} 
					
				})
			},
			/**
			 * @param {Object} e缩略图
			 */
			doBefore() {
				console.log('doBefore');
			},
			clk(index) {
				this.$refs.avatar.fChooseImg(index,{
					selWidth: '300upx', selHeight: '300upx', 
					expWidth: '200upx', expHeight: '200upx',
					inner: index ? 'true' : 'false'
				});
			},
			doUpload(rsp) {
				var _self = this
				console.log(rsp.path);
				//this.$set(this.urls, rsp.index, rsp.path);
				uni.uploadFile({
					url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
					//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
					filePath: rsp.path,
					name: 'file', 
					formData: {
						'user': 'test'
					},
					success: (uploadFileRes) => {
						console.log(uploadFileRes.data)
						_self.smallPrimary = uploadFileRes.data.replace("\"","").replace("\"","")
					}
				  }); 
			},
			/**
			 * @param {Object} e删除图片
			 */
			deletePic:function(e){
				var _self = this
				console.log(e)
				// _self.imageList.splice(e,1)
				_self.pathArr.splice(e,1)
				console.log(_self.imageList)
			},
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value 
			},
			previewImage: function(e) {
				console.log(this.imageList)
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			}, 
			chooseImage: async function() {
				var _self = this
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						success: (res) => {
							console.log(res)
							console.log(JSON.stringify(res.tempFilePaths));
							var tempFilePaths = res.tempFilePaths;
							this.imageList = this.imageList.concat(res.tempFilePaths);
							for(var i =0;i<tempFilePaths.length;i++){
							uni.uploadFile({
								url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
								//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
								filePath: tempFilePaths[i],
								name: 'file', 
								formData: {
									'user': 'test'
								},
								success: (uploadFileRes) => {
									_self.pathArr = _self.pathArr.concat(JSON.parse(uploadFileRes.data))
									console.log(_self.pathArr)
								}
							  }); 
							}
							_self.state = true
						} 
					})
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
			showOrHide:function(){
					this.showBox = false
				},
			addInfo:function(){
				var _self = this
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
				if(_self.title == ""){
					uni.showToast({
						title:"请输入标题",
						icon:"none"
					})
					return false
				}
				// else if(_self.state == false && _self.imageList.length>0){ 
				// 	uni.showToast({
				// 		title:"图片正在上传，请稍后提交1",
				// 		icon:"none"  
				// 	})
				// 	return false
				// }
				else if(_self.textarea == ""){
					uni.showToast({
						title:"请输入简介",
						icon:"none"
					})
					return false
				}
				else if(_self.phone == ""){
					uni.showToast({
						title:"请输入联系方式",
						icon:"none"
					})
					return false
				}
				else if(_self.smallPrimary == ""){
					uni.showToast({
						title:"请上传缩略图",
						icon:"none"
					})
					return false
				}
				else if(!myreg.test(_self.phone)){
						uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}
					for(var i =0;i<_self.pathArr.length;i++){
						_self.listPath.push({testName1:_self.pathArr[i]})
						console.log(_self.listPath)
					}
					_self.listPath.push({phone:_self.phone,mark:_self.id,name:_self.title,sketch:_self.textarea,optionId:uni.getStorageSync("openId"),branch:uni.getStorageSync('tradeId'),trade:0,smallPrimary:_self.smallPrimary})
					console.log(JSON.stringify(_self.listPath))
				uni.request({
					url:_self.$api+"dockingManager/titleDeclareAdd", 
					data:JSON.stringify(_self.listPath), 
					method:"POST",
					success:function(res){ 
						if(res.data ==98){
							uni.showToast({
								title:"您尚未认证",
								icon:'none'
							})
							return false
						}else if(res.data ==99){
							uni.showModal({
							title: '提示',
							content: '此操作需用户授权，是否进行授权',
							success: function (res) {
								if (res.confirm) {
									//跳转到授权页面  
									uni.navigateTo({
										url:"../../login/login"  
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
		
							return false
						}else if(res.data ===0){
								uni.showToast({
								title:"发布失败"
						})
							return false
						}else if(res.data ==1){
								uni.showToast({
								title:"发布成功",
								success:function(){
									uni.navigateBack({
										delta: 1,
										animationType: 'pop-out',
										animationDuration: 100
									});
								}
							})
							return false
						}else{
							uni.showToast({
								title:"系统异常"
							})
							return false
						}
						console.log(res)
					} 
					
				})
			}
		},
		 components: {uniCollapse,uniCollapseItem,avatar}
	}
